.singleChoice {
  height: 100%;
  // display: flex;
  // flex-direction: column;
  .exam_title_wrap {
    padding-bottom: 1em;
    padding: 12px 12px 0 12px;
    background: #fff;
    .exam_Type {
      border-radius: 2px;
      padding: 4px 8px;
      font-size: 15px;
      margin-right: 10px;
      background: #108ee9;
      color: #fff;
    }
    .exam_Title {
      margin-bottom: 6px;
      font-size: 17px;
      word-break: normal;
      word-wrap: break-word;
      color: #333;
      p {
        display: inline;
      }
      img {
        max-width: 100%;
        margin-top: 5px;
      }
      video {
        max-width: 100%;
      }
    }
    .item_score {
      display: inline-block;
      color: #999;
      margin-left: 5px;
      font-size: 15px;
    }
  }
  :global {
    .choose-wrap {
      width: 100%;
      padding: 24px 12px 0 12px;
      background: #fff;
      .choose {
        width: 100%;
        margin-right: 5px;
        margin-bottom: 24px;
        .ant-radio-wrapper {
          width: 100%;
          display: flex;
          white-space: normal;
          .ant-radio {
            display: none;
          }

          .choose-radio {
            width: 40px;
            height: 40px;
            border: 1px solid #999999;
            border-radius: 50%;
            display: flex;
            -ms-flex-pack: center;
            justify-content: center;
            -ms-flex-align: center;
            align-items: center;
            position: absolute;
          }

          .choose-split {
            display: flex;
            width: 100%;
            -ms-flex-pack: center;
            justify-content: center;
            box-sizing: border-box;
            line-height: 1em;
            -ms-flex-align: center;
            align-items: center;
          }

          .choose-description {
            display: inline-block;
            margin-left: 40px;
            padding: 10px;
            font-size: 15px;
            color: #666;
            line-height: 23px;
            img {
              max-width: 100%;
            }
            video {
              max-width: 100%;
            }

            * {
              text-indent: 0 !important;
            }
          }
        }
      }
      .hasChoose {
        .ant-radio-wrapper {
          .choose-radio {
            background: #108ee9;
            border: 1px solid #108ee9;
            color: #fff;
            .choose-split {
              border: none;
            }
          }
        }
      }
    }
  }
}

